@color: #ea2665;

@btn-val-color: white;

@width-100: 100%;

@padding-0: 0;

@margin-0: 0;

html {
    height: 100%;
    body {
        height: 100%;
    }
}

.advertisement {
    padding: @padding-0;
}

.index-tips {
    width: @width-100;
}

.tabs-index {
    padding: @padding-0;
    .am-tabs {
        margin: @margin-0;
    }
    .am-tabs-d2 .am-tabs-nav>.am-active {
        border-bottom: 3px solid @color;
        color: @color;
    }
    .am-tabs-d2 .am-tabs-nav>.am-active a{
        color: @color;
    }
    .am-tabs-d2 .am-tabs-nav>.am-active:after {
        border-bottom-color: @color;
    }

    .am-tab-panel {
        padding-top: 21px;
    }
}

.inputs {
    
    .am-form-group {
        // margin: 10px 0;
        height: 60px;
        margin: 0;
    }

    .am-form-textarea {
        height: auto;
        textarea {
            border-radius: 2px;
        }
    }
    .am-u-sm-12 {
        padding: @padding-0;
    }

    input[type=text], 
    input[type=password] {
        padding: .6em;
        font-size: 1.5rem;
        border-radius: 2px;
    }
    .am-btn-default {
        width: 100%;
        background-color: @color;
        color: @btn-val-color;
        border-radius: 2px;
        border: 0;
        padding: .6em 1em;
    }

    .cellphone-code {
        position: absolute;
        height: 35px;
        z-index: 1;
        right: 2px;
        top: 2px;
        font-size: 14px;
        padding-left: 5px;
        padding-right: 5px;
        background-color: @color;
        color: @btn-val-color;
        border-radius: 2px;
    }

    .code {
        position: absolute;
        width: 74px;
        height: auto;
        z-index: 1;
        right: 3px;
        top: 3px;
    }

    .err {
        height: 20px;
        img {
            width: 12px;
        }
        p {
            margin: 0;
            font-size: 12px;
            height: 12px;
            line-height: 12px;
            margin-left: 3px;
            color: @color;
        }
        
    }

    .am-field-error, 
    .am-form-error .am-form-field {
        border-color: @color!important;
        -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    }

}

.register-n {
    .active-introduction {
        width: 60%;

    }
    .am-u-sm-4 {
        padding-left: 2px;
        width: 40%;
        .am-u-sm-10 {
            padding-right: 2px;
        }
        .am-u-sm-2 {
            padding-left: 0px;
            padding-right: 0px;
        }
    }
}

.active-introduction {
    border-right: 1px solid #ccc;
    margin-top: 9px;
    p {
        margin-bottom: 0;
        margin-top: 0;
    }
    span {
        color: @color;
    }
}

.active-bonus {
    margin-top: 9px;
    
    p {
        margin-bottom: 0;
        margin-top: 0;

    }
    p:first-child {
        line-height: 18px;
    }
}

.go-to-active {
    margin-top: 9px;
    height: 37px;
    text-align: center;
    img {
        width: 12px;
    }
}

.software-tips {
    background-color: @color;
    color: white;
    margin-left: -1rem;
    margin-right: -1rem;
    margin-top: 9px;
    padding-left: 1rem;
    padding-right: 1rem;
    height: 20px;
    img {
        width: 14px;
        margin-right: 2px;
    }

    p {
        font-size: 12px;
        margin: 0;
        height: 12px;
        line-height: 12px;
    }
}

.inviter {
    margin: 4px 0;
    font-size: 12px;
    span {
        color: @color;
    }
}

.agreement {
    font-size: 12px;
    a {
        color: @color;
        text-decoration: none;
    }
}

.success-n {
    height: 100%;
    background-color: #fee5e6;
    .success-top-bar {
        background-color: white;
        padding: 10px 0;
        box-shadow: 0 3px 3px #ccc;
        .register-suc-word {
            text-align: center;
            height: 50px;
            img {
                width: 40px;
                margin-right: 10px;
            }

            p {
                font-size: 20px;
                margin: 0;
            }

        }
        .software-down {
            text-align: center;
            height: 60px;
            button {
                width: 50%;
                background-color: @color;
                color: @btn-val-color;
                border-radius: 2px;
                border: 0;
                padding: .6em 1em;
            }
        }
    }

    .success-bottom {
        text-align: center;
        margin-top: 30px;
        .success-bottom-text1 {
            color:@color;
            font-size: 21px;
            font-family: 'Microsoft YaHei';
        }
        img {
            width: 180px;
        }

        .success-bottom-text2 {
            color: #717171;
            font-family: 'Microsoft YaHei';
        }
    }

    .share {
        position: fixed;
        z-index: 2;
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
        }
    }
}

.invitation-code {

    .invitation-back {
        background-image: url(../img/invitation-back.jpg);
        background-size: 100%;
        background-repeat: no-repeat;
        height: 237px;
        padding: 0;
        .gift-tips {
            height: 36px;
            background-color: white;
            img {
                width: 28px;
                margin: 0 5px;

            }

            p {
                font-size: 16px;
                margin-bottom: 0;
                margin-top: 0;
                color: @color;
            }
        }
        .down-pics {
            background-image: url(../img/down-pic-back.png);
            background-size: 100%;
            background-repeat: no-repeat;
            height: 42px;
            p {
                margin-bottom: 0;
                margin-right: 10px;
                font-size: 17px;
            }
            a {
                font-size: 12px;
                text-decoration: underline;
                line-height: 27px;
            }
        }
        .my-invitation-code {
            text-align: center;
            p {
                margin: 0;
            }
            img {
                width: 110px;
            }
            a {
                display: block;
                color: #A9A9A9;
                font-size: 12px;
            }

        }
    }

    .invitation-form {
        background-color: @color;
        .invitation-success {
            height: 57px;
            text-align: center;
            margin: 0 -12px;
            background-color: white;
            display: none;
            img {
                width: 30px;
                margin-right: 10px;
            }

            p {
                margin: 0;
                line-height: 30px;
                font-size: 15px;
            }
        }
        .am-form-group {
            position: relative;
            margin: 10px 0;
            .am-form-group-input {
                margin-right: 55px;
                input {
                    font-size: 15px;
                    height: 37px;
                    border-radius: 3px;
                }
            }
            img {
                position: absolute;
                right: 50px;
                width: 24px;
                top: 8px;
                display: none;
            }
            button {
                position: absolute;
                right: 0;
                top: 0;
                width: 50px;
                color: white;
                background-color: @color;
                border-color: @color;
                font-size: 19px;
                line-height: 33px;
                padding: 1px 6px;
            }
        }

        .invitation-err {
            img {
                display: block;
            }
            .am-form-group-input {
                margin-right: 80px;
            }
        }
    }

    .invitation-form-success {
        .invitation-success {
            display: block;
        }

        .am-form-inline {
            display: none;
        }
    }
    .invitation-person {
        padding: 0;
        .invitation-person-total {
            background-color: #EFEFEF;
            p {
                font-size: 13px;
                margin-bottom: 0;
                line-height: 30px;
                
            }
        }

        .invitation-table {
            padding: 0;
            tbody>tr>td {
                padding: 5px 13px;
                font-size: 14px;
                color: #666;
            }
            .am-table  {
                margin-bottom: 0;
            }
        }

        .page {
            border-top: 1px solid #ddd;
            .am-pagination {
                margin:10px 0;
                a {
                    border-radius: 1000px;
                    background-color: white;
                    border: 1px solid #ddd;
                    font-size: 13px;
                    color: @color;
                }

                select {
                    border-radius: 1000px;
                    background-color: white;
                    border: 1px solid #ddd;
                    font-size: 13px;
                    color: @color;
                }
            }
        }
    }
}

.appointment-cities {
    margin-top: 10px;
    p {
        font-size: 12px;
        margin-bottom: 3px;
    }

    .appointment-city {
        border: 1px solid #D8D8D8;
        padding: 3px 10px;
        font-size: 12px;
        border-radius: 2px;
        display: inline-block;
        color: @color;
    }
    .appointment-city-active {
        border: 1px solid @color;
        color: white;
        background-color: @color;
    }
}

.appointment-title {
    h4 {
        margin: 5px 0;
    }
}


.am-container {
    .choice-city {
        position: fixed;
        height: 100%;
        overflow: hidden;
        background-color: white;
        z-index: 3;
        top: 0;
        width: 100%;
        left: 100%;
        margin: 0;

        .choice-city-title {
            background-color: @color;
            position: fixed;
            padding: 12px 0;
            z-index: 5;
            p {
                color: white;
                text-align: center;
                margin-bottom: 0;
                line-height: 18px;
                font-size: 18px
            }
            img {
                position: absolute;
                top: 12px;
                right: 15px;
                width: 18px;
            }
        }

        .choice-city-bar {
            position: absolute;
            height: 100%;
            padding: 42px 0 0 0;
            // border-top: 1px solid #ccc;
            // border-left: 1px solid #ccc;
            .province {
                padding: 0;
                height: 100%;
                overflow: auto;
                div {
                    width: 100%;
                    padding: 10px 0;
                    text-align: center;
                    border-right: 1px solid #ccc;
                    border-bottom: 1px solid #ccc;
                }
                .city-active {
                    background-color: #E0DDDD;
                    color: @color;
                }
            }
            .city {
                .province;
            }

            .county {
                .province;
            }
        }

        .choice-city-submit {
            position: absolute;
            bottom: 0;
            z-index: 12;
            padding: 0;
            .submit-back {
                background-color: black;
                opacity: 0.5;
                height: 60px;
                padding: 0;
            }
            .choice-city-submit-btn {
                position: absolute;
                text-align: center;
                top: 50%;
                margin-top: -18px;
                button {
                    width: 180px;
                    background-color: @color;
                    color: white;
                    border-color: @color;

                }
            }
        }
    }


}

#my-popup {
    .am-popup-inner {
        padding: 0;
        .am-popup-bd {
            padding: 0;
            height: 100%;
        }
    }
}

#choice-city-input {
    padding: .6em;
    font-size: 1.5rem;
    border-radius: 2px;
    display: block;
    width: 100%;
    line-height: 1.2;
    color: #9E9E9E;
    vertical-align: middle;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.city-map {
    #mapContainer {
        position: absolute;
        top:0;
        left: 0;
        right:0;
        bottom:0;
        .amap-logo {
            display: none;
        }
        .amap-copyright {
            display: none;
        }
    }
    #tip{
        background-color:#DCDCDC;
        border:1px solid #DCDCDC;
        position:fixed;
        top: 0;
        left: 0;
        font-size:12px;
        overflow:hidden;
        width: 100%;
    }
    #tip input[type="button"]{
        background-color: #0D9BF2;
        height:25px;
        text-align:center;
        line-height:25px;
        color:#fff;
        font-size:12px;
        border-radius:3px;
        outline: none;
        border:0;
        cursor:pointer;
    }
    #tip input[type="text"]{
        border:1px solid #ccc;
        border-radius:5px;
        outline:none;
        margin: 10px auto;
        width: 92%;
    }
    #result1{
        background-color: white;
        max-height: 382px;
        .result-list {
            width: 92%;
            margin: 0 auto;
        }
    }
    .choice-city-submit {
        position: absolute;
        bottom: 0;
        z-index: 12;
        padding: 0;
        .submit-back {
            background-color: black;
            opacity: 0.5;
            height: 60px;
            padding: 0;
        }
        .choice-city-detail-submit-btn {
            position: absolute;
            text-align: center;
            top: 50%;
            margin-top: -18px;
            button {
                width: 180px;
                background-color: @color;
                color: white;
                border-color: @color;

            }
        }
    }  
}

.pain-close {
    display: none;
}

.pain-open {
    display: block;
}

.datetimepicker {
    position: fixed;
    top: 50%;
    z-index: 9999;
    margin-left: -119px;
    margin-top: -142px;
}


.datetimepicker:before {
    content: '';
    display: block;
    display: inline-block;
    width: 0;
    height: 0;
    vertical-align: middle;
    border-bottom: 0px solid #3bb4f2;
    border-right: 0px solid transparent;
    border-left: 0px solid transparent;
    border-top: 0 dotted;
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
    position: absolute;
}

// .datetimepicker {
//     display: none;
// }

@media only screen and (max-width: 640px) {
    .datetimepicker {
        width: 238px;
        left: 50%!important;
    }
}

.datetimepicker-background {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.5;
    z-index: 20;
    display: none;
    top: 0;
    left: 0;
}

.datetimepicker table {
    background-color: @color;
}

.datetimepicker table th.next, .datetimepicker table th.prev {
    background-color: @color;
}

.datetimepicker table th.switch {
    background-color: @color;
}

.datetimepicker table th.dow {
    color: @color;
}

.demand {
    width: 280px;
    position: fixed;
    top: 0;
    .title {
        width: 100%;
        background-color: @color;
        text-align: center;
        position: relative;
        p {
            color: white;
            font-size: 16px;
            line-height: 36px;
            margin: 0;
        }
        img {
            position: absolute;
            right: 10px;
            width: 16px;
            top: 11px;
        }

    }
}



